import Image from "next/image";
import Link from "next/link";
import { getArticles } from "@/lib/getArticles";

export default async function NewsPage() {
  const data = await getArticles(1);
  const articles = data.data;
  const pagination = data.meta.pagination;

  return (
    <div className="max-w-7xl mx-auto py-16 px-6">
      <h1 className="text-4xl font-bold mb-10 text-center">Latest News</h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
        {articles.map((article: any) => {
          const img = article.attributes.image?.data?.attributes?.url;
          const author = article.attributes.author || "Unknown";

          return (
            <div
              key={article.id}
              className="rounded-2xl overflow-hidden bg-white shadow-md"
            >
              {img && (
                <Image
                  src={`http://localhost:1337${img}`}
                  alt="Article image"
                  width={400}
                  height={250}
                  className="w-full h-56 object-cover"
                />
              )}

              <div className="p-4">
                <h2 className="font-semibold text-xl">
                  {article.attributes.title}
                </h2>
                <p className="text-gray-600 text-sm mb-3">By {author}</p>
                <p className="text-gray-700 line-clamp-3">
                  {article.attributes.description}
                </p>
              </div>
            </div>
          );
        })}
      </div>

      {/* Pagination button */}
      {pagination.pageCount > 1 && (
        <div className="flex justify-center mt-12">
          <Link
            href={`/news/2`}
            className="px-6 py-3 bg-black text-white rounded-xl"
          >
            Next Page â†’
          </Link>
        </div>
      )}
    </div>
  );
}
